import React from "react";
import { styled } from "@mui/material/styles";
import Typography from "@mui/material/Typography";
import { Parallax } from "react-scroll-parallax";
import  Container  from "@mui/system/Container";
import Paper from "@mui/material/Paper";
import  {alpha} from "@mui/material/styles";

const StyledTitle = styled(Typography)(({ theme }) => ({
  color: "white",
}));

const Title = ({ ...props }) => {
  return (
    <Parallax opacity={[0, 1, "easeOutQuint"]} >
      <StyledTitle variant="h3" component={"div"} align="center" {...props} />
    </Parallax>
  );
};

const StyledDescription = styled(Typography)(({ theme }) => ({
    backgroundColor:"#0a0a0a38"
}));

const DescriptionPaper = styled(Paper)(({theme})=>({
    // backgroundColor: `rgba(${theme.palette.background.paper} / 0.12 )`
    backgroundColor: alpha(theme.palette.background.paper,0.8),
    padding:theme.spacing(3,2),
    textAlign:"center",
    marginBottom:theme.spacing(5),
}));
//<StyledDescription component={"div"} align="center" {...props} />
const Description = ({...props}) => {
  return (
    <Parallax opacity={[0, 1, "easeOutQuint"]} style={{display:"flex",justifyContent:"center"}}>
    <DescriptionPaper  {...props}/>
    </Parallax>
  );
};

const AboutMe = () => {
  return (
    <Container fixed maxWidth="lg" id="aboutMe">
      <Title gutterBottom>Kto to jest?</Title>
      <Description sx={{width:"70ch"}}>
      {/* <Typography variant="h5">Jestem Łukasz</Typography> */}
      <Typography variant="h5" gutterBottom >Mam na imię Łukasz</Typography>
       Uwielbiam rozwiązywać problemy i także tworzyć rzeczy proste w użyciu. Nigdy nie przestaję się uczyć nowych rzeczy; im więcej tym lepiej. Interesuję się elektroniką i elektromechaniką, która jest dla mnie inspiracją.
      </Description>
      <Title gutterBottom>Czym się zajmuję?</Title>
      <Description sx={{width:"70ch"}}>
      {/* <Typography variant="h5">Jestem Łukasz</Typography> */}
      <Typography variant="h5" gutterBottom >Jestem web developerem</Typography>
      <Typography gutterBottom>Na front-end'zie najczęściej korzystam z Javascript z użyciem frameworka React i biblioteki MaterialUI dla tworzenia responsywnych stron internetowych.</Typography>
      <Typography>Na back-end'zie najczęściej używam PHP (Wordpress, Laravel, itp). Jeżeli projekt tego wymaga korzystam z innych narzędzi w językach: Javascript, C# i innych języków "obiektowych". W projektach używam baz danych MS Sql Server, a także MariaDB. Moje aplikację opierają się na chmurach Amazon Web Services, Azure, Google Firebase.</Typography>
      </Description>
      
    </Container>
  );
};

export default AboutMe;
